
.header{
    height: 200px;
    background-image: url("../resources/banner-logo-marvel-2.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center center;
}


.form-container{
    border: black;
    border-radius: 0;
    height: 80px;
    width: 100%;
    background-color: black;
}

.hero-input{
    font-size: 1.5rem;
    margin-top: 10px;
    padding: 5px;
    width: 100%;
}



.hero-container{
    background-image: url('../resources/marvelcomics.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-attachment: scroll;
    display: flex;
    flex-wrap: wrap;
    background-position: center center;
    justify-content: space-around;
    width: 100%;
}


.hero-background{
    height: 100vh;
    background-image: url('../resources/marvelcomics.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}


.card-container{
    width: 380px;
    height: auto;
    margin: 10px;
  
}

.heroImage{
    width: 100%;
    height: 350px;
}

.icons{
    font-size: 20px;
}

.searching{
    position: relative;
    top: 70px;
    width:50%;
}

.not-found{
    margin: auto;
    position: relative;
    top: 50px;
    height: 350px;
    width: 80%;
    background-image: url('../resources/spidersense.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center center;
}

.not-found__help{
    display: flex;
    align-items: flex-end;
    justify-content: center;
    
}

.not-found__text {
    
    background-color: rgba(0, 0, 0, 0.7)
}

.not-found__text p{
    font-size: 24px;
    color: white;
    opacity: 1;
}

.popular-container{
    position: relative;
    top: 20px;
    margin: auto;
    width: 50%;
}

.footer{
    height: 75px;
}

.btn-page{
    height: 50px;
    width: 200px;
}

@media screen and (max-width:680px) {
    .header{
        height: 130px;
    }
    .not-found{
        width: 100%;
    }

    .not-found__text p{
        font-size: medium;
    }

    .card-container{
        width: 100%;
        height: auto;
    }

    .searching{
        height: 250px;
        width: 70%;
    }

    .popular-container{
        width: 100%;
    }

    .popular-list-item{
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;
    }

    .popular-container h4:nth-child(2){
        color: blueviolet;
        text-align: end;
    }

    .footer{
      
        height: 100%;
    }

    .btn-page{
        height: 70px;
        width: 125px;
    }

    .footer button:nth-child(2){
        position: relative;
        left: 15px;
    }
}


@media screen and (max-width:353px){
    .header{
        height: 100px;
    }
    .not-found__text p{
        font-size: small;
    }

    .popular-container{
        width: 100%;
    }
    .btn-search{
        margin-left: 15px;
    }

    .searching{
        height: 250px;
        width: 100%;
    }

    .btn-page{
        height: 70px;
        width: 105px;
    }

    .footer button:nth-child(2){
        position: relative;
        left: 15px;
    }

    
}